import { FormControl, TextField } from "@mui/material";
import { ChangeEvent } from "react";

export interface KillSelectorProps {
  kills: number;
  onKillsChange: (event: ChangeEvent<HTMLInputElement>) => void;
}

export const KillSelector = ({ kills, onKillsChange }: KillSelectorProps) => {
  return (
    <FormControl sx={{ mb: 3, width: 300 }}>
      <TextField
        id="kills"
        label="Kills To Simulate: "
        type="number"
        variant="standard"
        color="error"
        value={kills}
        onChange={onKillsChange}
        inputProps={{ min: 1, max: 1000000000 }}
        required
      />
    </FormControl>
  );
};
