import { LoadingButton } from "@mui/lab";
import { InputLabel, SelectChangeEvent, Step, StepLabel, Stepper } from "@mui/material";
import { ChangeEvent, FormEvent, useState } from "react";
import { DryStreakSelector } from "../components/DryStreakSelector";
import { EnrageSelect } from "../components/EnrageSelect";
import { ZammyData, EnrageRates, calculateBestEnrage, EnrageKillsPerHour } from "../utility/formula";

interface KillRange {
  first: number;
  last: number;
}

interface StepData {
  killRange: KillRange;
  enrage: string;
}

const spliceDict = (dict: Map<string, any>, minKey: number, maxKey: number) => {
  const newDict = new Map();
  const array = [...Array.from(dict.keys())];
  for (const [key, value] of dict) {
    const index = array.indexOf(key);
    if (index >= minKey && index <= maxKey) {
      newDict.set(key, value);
    }
  }
  return newDict;
};

export const OptimalEnragePage = () => {
  const [dryStreak, setDryStreak] = useState<number>(0);
  const [enrage, setEnrage] = useState<string>("");
  const [activeStep, setActiveStep] = useState<number>(0);
  const [steps, setSteps] = useState<StepData[]>([]);
  // const [kphMap, setKphMap] = useState<Map<string, number>>(EnrageKillsPerHour);
  const [array, setArray] = useState<Map<string, ZammyData>>(EnrageRates);
  // const [result, setResult] = useState<string>("");

  const onDryStreakChange = (event: ChangeEvent<HTMLInputElement>) => {
    setDryStreak(parseInt(event.target.value));
  };

  const onEnrageChange = (event: SelectChangeEvent) => {
    const val = event.target.value as string;
    setEnrage(val);
    setArray(spliceDict(EnrageRates, 0, [...Array.from(EnrageRates.keys())].indexOf(val)));
  };

  const onSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (enrage === null) {
      return;
    }

    const resultList: StepData[] = [];
    for (var i = 0; i < 100; i++) {
      const result = calculateBestEnrage(i, array, EnrageKillsPerHour);
      const previous = resultList[Math.max(resultList.length - 1, 0)];
      if (previous && previous.enrage === result.enrage) {
        resultList[Math.max(resultList.length - 1, 0)] = {
          enrage: previous.enrage,
          killRange: { first: previous.killRange.first, last: previous.killRange.last + 1 },
        };
      } else {
        resultList.push({
          enrage: result.enrage,
          killRange: {
            first: i,
            last: i,
          },
        });
      }
    }

    setActiveStep(resultList.findIndex((x) => x.killRange.first <= dryStreak && x.killRange.last >= dryStreak));
    setSteps(resultList);

    // const result = calculateBestEnrage(dryStreak, array, kphMap);
    // let paths = [];
    // for (var i = dryStreak + 1; i < 100; i++) {
    //   paths.push(`[${i}] ${calculateBestEnrage(i, array, kphMap).enrage}`);
    // }
    // setResult(`<h3>Optimal Enrage:<br />[${dryStreak}] ${result.enrage}<h3><h4>Next Kills: ${paths.join(" > ")}</h4>`);
  };

  return (
    <form
      style={{
        marginTop: "50px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
      }}
      onSubmit={onSubmit}
    >
      <DryStreakSelector dryStreak={dryStreak} onDryStreakChange={onDryStreakChange} />
      <EnrageSelect title="Your Max Enrage: " enrage={enrage} onEnrageChange={onEnrageChange} />
      {/* <Box
        sx={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          alignItems: "center",
          flexWrap: "wrap",
          maxWidth: "1500px",
        }}
      >
        {enrage &&
          [...Array.from(array.keys())].map((e) => (
            <KillsPerHourSelector
              key={e}
              id={e}
              title={`${e} - KPH`}
              killsPerHour={kphMap.get(e) ?? 1}
              sx={{ margin: "0 20px 0 20px" }}
              onKillsPerHourChange={onKillsPerHourChange}
            />
          ))}
      </Box> */}
      <LoadingButton type="submit" variant="outlined" color="error">
        Get Optimal Enrage Path
      </LoadingButton>
      <Stepper
        activeStep={activeStep}
        alternativeLabel
        sx={{
          display: "flex",
          flexDirection: "row",
          width: "90vw",
          flexWrap: "wrap",
          justifyContent: "center",
          alignItems: "center",
          marginTop: "30px",
          marginBottom: "30px",
        }}
      >
        {steps.map((label, index) => (
          <Step key={`${label.killRange.first}-${label.killRange.last}`}>
            <StepLabel
              StepIconProps={{
                style: {
                  color: index < activeStep ? "rgb(56, 142, 60)" : index === activeStep ? "white" : "#796237",
                },
              }}
            >
              <InputLabel
                style={{
                  color: index < activeStep ? "rgb(56, 142, 60)" : index === activeStep ? "white" : "#796237",
                }}
              >{`[${label.killRange.first}${
                index === steps.length - 1 ? "+" : `-${label.killRange.last}`
              }]`}</InputLabel>
              <span
                style={{
                  color: index < activeStep ? "rgb(56, 142, 60)" : index === activeStep ? "white" : "#796237",
                }}
              >{`Enrage:\u00A0${label.enrage}%`}</span>
            </StepLabel>
          </Step>
        ))}
      </Stepper>
      {/* <div style={{ marginTop: "10px" }} dangerouslySetInnerHTML={{ __html: result }}></div> */}
    </form>
  );
};
