export interface ZammyData {
  chance: number;
  bow: number;
  codex: number;
}

export const EnrageRates: Map<string, ZammyData> = new Map([
  // ["Story Mode", { chance: 500, bow: 0.125, codex: 0.35, kph: 20 }],
  ["0-19", { chance: 100, bow: 0.125, codex: 0.35 }],
  ["20-49", { chance: 99, bow: 0.125, codex: 0.35 }],
  ["50-99", { chance: 97, bow: 0.125, codex: 0.35 }],
  ["100-149", { chance: 80, bow: 0.15, codex: 0.325 }],
  ["150-199", { chance: 77, bow: 0.15, codex: 0.325 }],
  ["200-299", { chance: 72, bow: 0.15, codex: 0.325 }],
  ["300-399", { chance: 67, bow: 0.15, codex: 0.325 }],
  ["400-499", { chance: 62, bow: 0.15, codex: 0.325 }],
  ["500-749", { chance: 52, bow: 0.175, codex: 0.3 }],
  ["750-899", { chance: 47, bow: 0.185, codex: 0.29 }],
  ["900-999", { chance: 40, bow: 0.19, codex: 0.285 }],
  ["1000-1249", { chance: 37, bow: 0.2, codex: 0.275 }],
  ["1250-1499", { chance: 35, bow: 0.21, codex: 0.265 }],
  ["1500-1999", { chance: 31, bow: 0.21, codex: 0.265 }],
  ["2000+", { chance: 28, bow: 0.225, codex: 0.25 }],
]);

// taken from pvme
export const EnrageKillsPerHour: Map<string, number> = new Map([
  ["0-19", 13.85],
  ["20-49", 13.33],
  ["50-99", 12.86],
  ["100-149", 10.59],
  ["150-199", 10],
  ["200-299", 9.47],
  ["300-399", 8.57],
  ["400-499", 7.5],
  ["500-749", 7.13],
  ["750-899", 6.37],
  ["900-999", 4.62],
  ["1000-1249", 4.29],
  ["1250-1499", 3.87],
  ["1500-1999", 3.64],
  ["2000+", 3.43],
]);

export const vestmentRate = 0.36;
export const chaosRoarRate = 0.165;

const calculate = (enrage: string, simulations: number) => {
  const values = EnrageRates.get(enrage);
  if (!values) {
    return null;
  }

  const baseDropRate = values.chance;
  const bowPieceRate = values.bow;
  const codicesRate = values.codex;
  var dropRate = baseDropRate;
  var drop = 0;
  var blm = 0;

  const minimumKillsForBlm = 10;
  const maximumDropRate = 20;

  const loops = simulations;

  for (var i = 0; i < loops; i++) {
    var rng = Math.floor(dropRate * Math.random());

    if (rng === dropRate - 1) {
      // we got a drop
      drop += 1;
      // reset the drop rate
      dropRate = baseDropRate;
      // reset blm
      blm = 0;
    } else {
      // increase blm
      blm += 1;
      if (blm > minimumKillsForBlm) {
        // decrease drop rate by 1 until we get to 1/20
        dropRate -= 1;
        dropRate = Math.max(dropRate, maximumDropRate);
      }
    }
  }

  const bowPieces = drop * bowPieceRate;
  const codices = drop * codicesRate;

  var rate = Math.max(0, loops / drop);

  return {
    drops: drop,
    rate,
    bowPieces,
    codices,
    values,
    vestments: drop * vestmentRate,
    chaosRoars: drop * chaosRoarRate,
    kph: EnrageKillsPerHour.get(enrage),
  };
};

export const calculateBestEnrage = (
  currentDryStreak: number,
  inputData: Map<string, ZammyData>,
  killSpeeds: Map<string, number>
) => {
  /**
   * Formula:
   * 1-10 = 1/BASE_RATE
   * 11-N = 1/Math.max(BASE_RATE - (N - 10), 20)
   */
  let lowest = { enrage: "", secondsPerDrop: Infinity };
  inputData.forEach((data, enrage) => {
    const secondsPerKill = 3600 / killSpeeds.get(enrage)!;
    let dropRate, secondsPerDrop;
    if (currentDryStreak < 11) {
      dropRate = 1 / data.chance;
      secondsPerDrop = secondsPerKill / dropRate;
    } else {
      dropRate = 1 / Math.max(data.chance - (currentDryStreak - 10), 20);
      secondsPerDrop = secondsPerKill / dropRate;
    }

    if (lowest.secondsPerDrop > secondsPerDrop) {
      lowest = { enrage, secondsPerDrop };
    }
  });
  return lowest;
};

export default calculate;
