import { FormControl, InputLabel, Select, MenuItem, SelectChangeEvent } from "@mui/material";
import { EnrageRates } from "../utility/formula";

export interface EnrageSelectProps {
  title?: string;
  enrage: string;
  onEnrageChange: (event: SelectChangeEvent) => void;
}

export const EnrageSelect = ({ title, enrage, onEnrageChange }: EnrageSelectProps) => {
  return (
    <FormControl sx={{ m: 3, width: 300 }} component="fieldset" variant="standard">
      <InputLabel id="demo-simple-select-label" color="error">
        {title ?? "Enrage: "}
      </InputLabel>
      <Select value={enrage} onChange={onEnrageChange} inputProps={{ "aria-required": true }} required color="error">
        {[...Array.from(EnrageRates.keys())].map((e) => (
          <MenuItem key={e} value={e}>
            {e}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};
