import { Paper, Table, TableHead, TableRow, TableCell, TableBody, Button, Box } from "@mui/material";
import { EnrageRates } from "../utility/formula";

export interface DropRatesResult {
  enrage: string;
  kills: number;
  drops: number;
  rate: number;
  bowPieces: number;
  codices: number;
  vestments: number;
  chaosRoars: number;
}

export interface ResultsTableProps {
  title: string;
  rows: DropRatesResult[];
  onReset: () => void;
}

export const DropRatesTable = ({ title, rows, onReset }: ResultsTableProps) => {
  const numberWithCommas = (x: number, fixed: number = 3) => {
    return x
      .toFixed(fixed)
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <div style={{ paddingLeft: 20, paddingRight: 20 }}>
      <Box sx={{ display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center" }}>
        <h1 className="title">{title}</h1>
        <Button sx={{ ml: 2 }} size="small" color="error" variant="outlined" onClick={onReset}>
          Reset
        </Button>
      </Box>
      <Paper className="container">
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Enrage</TableCell>
              <TableCell align="right">Simulated Kills</TableCell>
              <TableCell align="right">No. Drops</TableCell>
              <TableCell align="right">Avg | Base Rate</TableCell>
              <TableCell align="right">Bow Pieces</TableCell>
              <TableCell align="right">Codices</TableCell>
              <TableCell align="right">Vestments</TableCell>
              <TableCell align="right">Chaos Roars</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(({ enrage, kills, drops, rate, bowPieces, codices, vestments, chaosRoars }, index) => (
              <TableRow key={index}>
                <TableCell component="th" scope="row" sortDirection="asc">
                  {enrage}
                </TableCell>
                <TableCell align="right">{numberWithCommas(kills, 0)}</TableCell>
                <TableCell align="right">{numberWithCommas(drops, 0)}</TableCell>
                <TableCell align="right">
                  {rate === Infinity ? "-" : `1/${numberWithCommas(rate)}`}
                  {` | 1/${EnrageRates.get(enrage)?.chance}`}
                </TableCell>
                <TableCell align="right">{numberWithCommas(bowPieces)}</TableCell>
                <TableCell align="right">{numberWithCommas(codices)}</TableCell>
                <TableCell align="right">{numberWithCommas(vestments)}</TableCell>
                <TableCell align="right">{numberWithCommas(chaosRoars)}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
};
