import { Paper, Table, TableHead, TableRow, TableCell, TableBody, Button, Box } from "@mui/material";
import { chaosRoarRate, EnrageRates, vestmentRate } from "../utility/formula";

export interface HoursPerDropResult {
  enrage: string;
  kills: number;
  killsPerHour: number;
  hoursPerDrop: number;
}

export interface HoursPerDropTableProps {
  title: string;
  rows: HoursPerDropResult[];
  onReset: () => void;
}

export const HoursPerDropTable = ({ title, rows, onReset }: HoursPerDropTableProps) => {
  const numberWithCommas = (x: number, fixed: number = 3) => {
    return x
      .toFixed(fixed)
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <div style={{ paddingLeft: 20, paddingRight: 20 }}>
      <Box sx={{ display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center" }}>
        <h1 className="title">{title}</h1>
        <Button sx={{ ml: 2 }} size="small" color="error" variant="outlined" onClick={onReset}>
          Reset
        </Button>
      </Box>
      <Paper className="container">
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Enrage</TableCell>
              <TableCell align="right">Total | Kills Per Hour</TableCell>
              <TableCell align="right">Hours Per Drop</TableCell>
              <TableCell align="right">Bow Pieces</TableCell>
              <TableCell align="right">Codices</TableCell>
              <TableCell align="right">Vestments</TableCell>
              <TableCell align="right">Chaos Roars</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(({ enrage, kills, killsPerHour, hoursPerDrop }, index) => (
              <TableRow key={index}>
                <TableCell component="th" scope="row">
                  {enrage}
                </TableCell>
                <TableCell align="right">{`${numberWithCommas(kills, 0)} | ${numberWithCommas(
                  killsPerHour,
                  2
                )}`}</TableCell>
                <TableCell align="right">{`${numberWithCommas(hoursPerDrop, 2)} hrs`}</TableCell>
                <TableCell align="right">
                  {`${numberWithCommas(hoursPerDrop / (EnrageRates.get(enrage)?.bow ?? 1), 2)} hrs`}
                </TableCell>
                <TableCell align="right">
                  {`${numberWithCommas(hoursPerDrop / (EnrageRates.get(enrage)?.codex ?? 1), 2)} hrs`}
                </TableCell>
                <TableCell align="right">{`${numberWithCommas(hoursPerDrop / vestmentRate, 2)} hrs`}</TableCell>
                <TableCell align="right">{`${numberWithCommas(hoursPerDrop / chaosRoarRate, 2)} hrs`}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
};
