import { ChangeEvent, FormEvent, useState } from "react";
import { Box, ButtonGroup, SelectChangeEvent } from "@mui/material";
import LoadingButton from "@mui/lab/LoadingButton";
import calculate, { EnrageKillsPerHour } from "../utility/formula";
import { DropRatesResult, DropRatesTable } from "./DropRatesTable";
import { EnrageSelect } from "./EnrageSelect";

import "../index.css";
import { KillSelector } from "./KillSelector";
import { KillsPerHourSelector } from "./KillsPerHourSelector";
import { HoursPerDropResult, HoursPerDropTable } from "./HoursPerDropTable";

const InputForm = () => {
  const [enrage, setEnrage] = useState<string>("");
  const [kills, setKills] = useState<number>(1);
  const [killsPerHour, setKillsPerHour] = useState<number>(1);
  const [loading, setLoading] = useState<boolean>(false);
  const [dropRates, setDropRates] = useState<DropRatesResult[]>([]);
  const [hoursPerDrop, setHoursPerDrop] = useState<HoursPerDropResult[]>([]);
  const [calcType, setCalcType] = useState<number>(0);

  const onEnrageChange = (event: SelectChangeEvent) => {
    const val = event.target.value as string;
    setEnrage(val);
    setKillsPerHour(EnrageKillsPerHour.get(val) ?? 1);
  };

  const onKillsChange = (event: ChangeEvent<HTMLInputElement>) => {
    setKills(parseInt(event.target.value));
  };

  const onKillsPerHourChange = (event: ChangeEvent<HTMLInputElement>) => {
    setKillsPerHour(parseFloat(event.target.value));
  };

  const onSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (enrage === null || kills === null) {
      return;
    }

    setLoading(true);
    const results = calculate(enrage, kills);
    setLoading(false);
    if (results) {
      switch (calcType) {
        case 0:
          setDropRates([{ ...results, enrage, kills }, ...dropRates]);
          break;
        case 1:
          setHoursPerDrop([
            {
              enrage,
              kills,
              killsPerHour,
              hoursPerDrop: results.rate / killsPerHour,
            },
            ...hoursPerDrop,
          ]);
          break;
      }
    }
  };

  return (
    <form onSubmit={onSubmit}>
      <Box sx={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
        <EnrageSelect enrage={enrage} onEnrageChange={onEnrageChange} />
        <KillSelector kills={kills} onKillsChange={onKillsChange} />
        <KillsPerHourSelector killsPerHour={killsPerHour} onKillsPerHourChange={onKillsPerHourChange} />
        <Box sx={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>
          <ButtonGroup variant="outlined" sx={{ pl: 3, pr: 3 }}>
            <LoadingButton
              sx={{ maxWidth: 250 }}
              onClick={() => setCalcType(0)}
              type="submit"
              loading={loading}
              variant="outlined"
              color="error"
            >
              Calculate Drop Rates
            </LoadingButton>
            <LoadingButton
              sx={{ maxWidth: 250 }}
              onClick={() => setCalcType(1)}
              type="submit"
              loading={loading}
              variant="outlined"
              color="error"
            >
              Calculate Hours Per Drop
            </LoadingButton>
          </ButtonGroup>
        </Box>
      </Box>
      <DropRatesTable title={"Drop Rates"} rows={dropRates} onReset={() => setDropRates([])} />
      <HoursPerDropTable title={"Hours Per Drop"} rows={hoursPerDrop} onReset={() => setHoursPerDrop([])} />
    </form>
  );
};

export default InputForm;
