import { FormControl, SxProps, TextField, Theme } from "@mui/material";
import { ChangeEvent } from "react";

export interface KillsPerHourProps {
  id?: string;
  title?: string;
  killsPerHour: number;
  sx?: SxProps<Theme>;
  onKillsPerHourChange: (event: ChangeEvent<HTMLInputElement>) => void;
}

export const KillsPerHourSelector = ({ id, title, killsPerHour, sx, onKillsPerHourChange }: KillsPerHourProps) => {
  return (
    <FormControl sx={{ mb: 3, width: 300 }}>
      <TextField
        id={id}
        label={title ?? "Kills Per Hour"}
        type="number"
        variant="standard"
        color="error"
        value={killsPerHour}
        onChange={onKillsPerHourChange}
        inputProps={{ min: 1, max: 200, step: 0.01 }}
        sx={sx}
        required
      />
    </FormControl>
  );
};
