import { FormControl, TextField } from "@mui/material";
import { ChangeEvent } from "react";

export interface DryStreakSelectorProps {
  dryStreak: number;
  onDryStreakChange: (event: ChangeEvent<HTMLInputElement>) => void;
}

export const DryStreakSelector = ({ dryStreak, onDryStreakChange }: DryStreakSelectorProps) => {
  return (
    <FormControl sx={{ mb: 3, width: 300 }}>
      <TextField
        id="dry_streak"
        label="Current Dry Streak: "
        type="number"
        variant="standard"
        color="error"
        value={dryStreak}
        onChange={onDryStreakChange}
        inputProps={{ min: 0 }}
        required
      />
    </FormControl>
  );
};
